.account-item a,
.account-item a:focus {
	color: #B2956D;
	text-decoration: none;
}

.account-item a:hover {
	text-decoration: underline;
}

.dl-link,
.dl-link:focus {
	display: flex;
	border: 1px solid #B2956D;
	border-radius: 0 25px 0 0;
	text-decoration: none !important;
	color: #B2956D;
	transition: all .2s ease;
	padding: 21px 30px 19px 12px;
	width: 350px;
}

.dl-link:hover {
	color: #fff;
	background: #B2956D;
}

.dl-link-ico {
	width: 39px;
	height: 41px;
	position: relative;
	flex-grow: 0;
	flex-shrink: 0;
	margin: -3px 20px 0 0;
}

.dl-link-ico:before {
	content: "";
	position: absolute;
	left: 0;
	right: 0;
	top: 0;
	bottom: 0;
	background: url(../images/dl-link.svg) 0 0 no-repeat;
	transition: all .2s ease;
}

.dl-link-ico:after {
	content: "";
	position: absolute;
	left: 0;
	right: 0;
	top: 0;
	bottom: 0;
	background: url(../images/dl-link-hover.svg) 0 0 no-repeat;
	transition: all .2s ease;
	opacity: 0;
}

.dl-link:hover .dl-link-ico:before {
	opacity: 0;
}

.dl-link:hover .dl-link-ico:after {
	opacity: 1;
}

.dl-link-name {
	font-size: 16px;
	line-height: 1.2;
	font-weight: bold;
	margin: 0 0 4px 0;
}

.dl-link-date {
	font-size: 13px;
	line-height: 1.2;
	color: #C6B8A1;
	transition: all .2s ease;
}

.dl-link:hover .dl-link-date {
	color: #fff;
}

.form-group-no-padding {
	padding-top: 0 !important;
}

.count-wrapper {
	display: flex;
	flex-wrap: nowrap;
}

.count-minus,
.count-plus {
	position: absolute;
	width: 21px;
	height: 21px;
	cursor: pointer;
	top: 7px;
}

.count-minus {
	left: 0;
}

.count-plus {
	right: 0;
}

.count-minus:before {
	content: "";
	height: 1px;
	background: #414141;
	position: absolute;
	top: 10px;
	left: 0;
	width: 21px;
	transition: all .2s ease;
}

.count-plus:before {
	content: "";
	height: 1px;
	background: #414141;
	position: absolute;
	top: 10px;
	left: 0;
	width: 21px;
	transition: all .2s ease;
}

.count-plus:after {
	content: "";
	width: 1px;
	background: #414141;
	position: absolute;
	top: 0;
	left: 10px;
	height: 21px;
	transition: all .2s ease;
}

.count-minus:hover:before,
.count-plus:hover:before,
.count-plus:hover:after {
	background: #b2956d;
}

.count {
	position: relative;
	width: 160px;
}

.count input[type=text] {
	font-weight: 450;
	text-align: center;
	padding-left: 35px;
	padding-right: 35px;
}

.count-units {
	font-size: 20px;
	font-weight: 450;
	margin: 3px 0 0 29px;
}

// Bootstrap select

.bootstrap-select.btn-group {
	display: block !important;
}

.bootstrap-select > .dropdown-toggle,
.bootstrap-select > .dropdown-toggle:focus {
	display: block;
	width: 100%;
	background: transparent;
	border: none;
	border-bottom: 1px solid #2A2723;
	-webkit-appearance: none;
	font-weight: normal;
	color: #000;
	padding: 4px 40px 15px 0;
	outline: none !important;
	font-size: 0;
	line-height: 0;
}

.bootstrap-select.btn-group-error > .dropdown-toggle,
.bootstrap-select.btn-group-error > .dropdown-toggle:focus {
	border-bottom-color: #D13C3C;
}

.bootstrap-select.show > .dropdown-toggle,
.bootstrap-select.show > .dropdown-toggle:focus {
	//border-bottom-color: #479BFF;
}

.bootstrap-select > .dropdown-toggle.bs-placeholder, .bootstrap-select > .dropdown-toggle.bs-placeholder:active, .bootstrap-select > .dropdown-toggle.bs-placeholder:focus, .bootstrap-select > .dropdown-toggle.bs-placeholder:hover {
	color: #000;
}

.bootstrap-select.btn-group .dropdown-toggle .filter-option {
	font-weight: 450;
	font-size: 16px;
	line-height: 1.5;
	color: #2A2723;
}

.bootstrap-select.btn-group .dropdown-toggle.bs-placeholder .filter-option {
	color: rgba(42,39,35,.5);
	font-weight: normal;
}

.bootstrap-select.btn-group .dropdown-toggle .caret {
	width: 13px;
	height: 9px;
	background: none;
	position: absolute;
	right: 0;
	top: 13px;
	margin: 0;
	display: block;
	vertical-align: top;
}

.bootstrap-select.btn-group .dropdown-toggle .caret:before {
	content: "";
	display: block !important;
	width: 13px;
	height: 8px;
	background: url(../images/select-arrow.svg) 0 0 no-repeat;
	background-size: 13px auto;
	position: absolute;
	left: 0;
	top: 0;
	transition: all .15s ease;
	opacity: 1;
}

.bootstrap-select:not([class*=col-]):not([class*=form-control]):not(.input-group-btn) {
	width: 100%;
}

.bootstrap-select.btn-group div.dropdown-menu {
	margin: -1px 0 0 0;
	padding: 0 0;
	border-radius: 0;
	border: none;
	box-shadow: 0px 4px 55px rgba(0, 0, 0, 0.1);
	border-top: 1px solid #B2956D;
}

.bootstrap-select.btn-group .dropdown-menu.inner {
	display: block;
}

.bootstrap-select.btn-group .dropdown-menu li {
	padding: 0 !important;
	margin: 0 !important;
	font-size: 16px;
	line-height: 1.5;
	font-weight: 450;
}

.bootstrap-select.btn-group .dropdown-menu li.disabled {
	display: none;
}

.bootstrap-select.btn-group .dropdown-menu li:before {
	display: none !important;
}

.bootstrap-select.btn-group .dropdown-menu li a {
	display: block;
	padding: 12px 17px 12px 20px;
	color: #000;
	position: relative;
	outline: none !important;
}

.bootstrap-select.btn-group .dropdown-menu li a:after {
	content: "";
	background: url(../images/select-check.svg) 0 0 no-repeat;
	width: 15px;
	height: 12px;
	position: absolute;
	right: 20px;
	top: 20px;
	opacity: 0;
}

.bootstrap-select.btn-group .dropdown-menu li a:hover {
	background: rgba(196, 196, 196, 0.1);
	color: #B2956D;
}

.bootstrap-select.btn-group .dropdown-menu li.selected a {
	background: rgba(196, 196, 196, 0.1);
	color: #B2956D;
}

.bootstrap-select.btn-group .dropdown-menu li.selected a:after {
	opacity: 1;
}

div.dropdown-menu {
	z-index: 10000;
}

